

document.addEventListener('DOMContentLoaded', function() {

    let form = document.querySelector('.calc')

    // валидация
    Pristine.addValidator("my-range", function(value, param1, param2) {
        return parseInt(param1) <= value && value <= parseInt(param2) 
    }, "Значение должно быть не больше ${2}", 5, false);


    Pristine.addValidator("my-range2", function(value) {
        return value.indexOf('_') == -1
    }, "Телефон указан неверно", 5, false);

    var pristine = new Pristine(form,{
        classTo: 'form-group',
        errorClass: 'error',
        successClass: 'has-success',
        // class of the parent element where error text element is appended
        errorTextParent: 'form-group',
        // type of element to create for the error text
        errorTextTag: 'div',
        // class of the error text element
        errorTextClass: 'error__text' 
    });
    
    form.addEventListener('submit', function (e) {
        e.preventDefault();
        
        // check if the form is valid
        var valid = pristine.validate(); // returns true or false
        if(valid){
            form.classList.add('active')
        }
    });


    Inputmask({"mask": "+7 (999) 999-99-99"}).mask(document.querySelectorAll(".phone"));


    // Калькулятор Своё дело. КАСКО
    if(document.getElementById('form1')){

        // Поля формы
        let form1__typeInput = document.getElementById('form1__typeInput').querySelectorAll('input')
        let form1__type = document.querySelectorAll('.form1__type')
        let form1__type__arr = [
            [1.8, 2.5, 1, 4],
            [1.08, 1.5, 0.6, 2.4],
        ]
        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 
        let priceInput = document.getElementById('price')

        // подписка на события
        priceInput.addEventListener('input', ()=>{
            formCounter();
        })

        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))

        form1__type.forEach(el=> el.addEventListener('change', ()=>{
            refreshCoef(el)
        }))

        // Изменить кофициенты
        const refreshCoef = (el)=>{
            let val = el.getAttribute('value')

            form1__typeInput.forEach((input, index)=>{
                input.setAttribute('value', form1__type__arr[val][index])
            })

            formCounter();
        }
        
        // Расчет премии
        const formCounter = () =>{
            let coef = document.getElementById('form1__typeInput').querySelector('input:checked').value
            let price = document.getElementById('price').value

            let totalPrice = coef / 100 * price
            totalPrice = totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();

    }

    // Калькулятор Своё дело.
    // Спецтехника
    if(document.getElementById('form2')){
        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 

        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))


        const formCounter = () =>{
            let price = document.getElementById('price').value
            let coef = 0.13;
            let count = document.querySelectorAll('.form2__row').length

            let totalPrice = price * coef * count;
            totalPrice =  totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();

        let addTech = document.getElementById('add-tech')
        let htmlBlock = document.querySelector('.form2__row');

        document.addEventListener('click', (e)=>{
            
            if(e.target.closest('.delete')){
                e.target.closest('.form2__row').remove()

                addTech.closest('.calc-add').classList.remove('max')
                formCounter();
            }
        })

        addTech.addEventListener('click', ()=>{
            addTechF()
        }) 

        const addTechF = ()=>{
            let count = document.querySelectorAll('.form2__row').length
            let htmlBlockClone = htmlBlock.cloneNode(true)
            
            if(count <= 4){
                document.querySelector('.form2__row-wrap').appendChild(htmlBlockClone)
                document.querySelector('.form2__row-wrap').classList.add('active')
                if(count == 4){
                    addTech.closest('.calc-add').classList.add('max')
                }

                formCounter();

                pristine = new Pristine(form,{
                    classTo: 'form-group',
                    errorClass: 'error',
                    successClass: 'has-success',
                    // class of the parent element where error text element is appended
                    errorTextParent: 'form-group',
                    // type of element to create for the error text
                    errorTextTag: 'div',
                    // class of the error text element
                    errorTextClass: 'error__text' 
                });
            }
        }
    }

    // Калькулятор Своё дело.
    // Грузоперевозки
    if(document.getElementById('form3')){

        let total = document.getElementById('total')
        let totalInput = document.getElementById('total-input') 

        let form3Variats = document.getElementById('form3Variat').querySelectorAll('input')
        
        let form3__type__arr = [
            [0.0012, 0.0009, 0.0005, 0.0007],
            [0.002, 0.0017, 0.0013, 0.0015],
        ]
        let form3__typeInputs = document.getElementById('form3__typeInputs').querySelectorAll('input')


        form3Variats.forEach(el=> el.addEventListener('change', ()=>{
            refreshCoef3(el)
        }))

        // Изменить кофициенты
        const refreshCoef3 = (el)=>{
            let val = el.getAttribute('value')

            form3__typeInputs.forEach((input, index)=>{
                input.setAttribute('value', form3__type__arr[val][index])
            })

            formCounter();
        }


        const formCounter = () =>{
            let price = document.getElementById('price').value
            
            let coef110 = 1; 
            if(document.getElementById('varian110').checked){
                coef110 = 1.1
            }

            let coef = document.getElementById('form3__typeInputs').querySelector('input:checked').value

            
            let totalPrice = price * coef110 * coef;
            totalPrice = totalPrice.toFixed(2)

            total.textContent = totalPrice;
            totalInput.setAttribute('value', totalPrice)
        }

        formCounter();


        form.querySelectorAll('input').forEach(el=> el.addEventListener('change', ()=>{
            formCounter();
        }))

        form.querySelectorAll('input').forEach(el=> el.addEventListener('input', ()=>{
            formCounter();
        }))

        Datepicker.locales.ru = {
            days: ["Воскресенье", "Понедельник", "Вторник", "Среда", "Четверг", "Пятница", "Суббота"],
            daysShort: ["Вск", "Пнд", "Втр", "Срд", "Чтв", "Птн", "Суб"],
            daysMin: ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"],
            months: ["Январь", "Февраль", "Март", "Апрель", "Май", "Июнь", "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"],
            monthsShort: ["Янв", "Фев", "Мар", "Апр", "Май", "Июн", "Июл", "Авг", "Сен", "Окт", "Ноя", "Дек"],
            today: "Сегодня",
            clear: "Очистить",
            format: "dd.mm.yyyy",
            weekStart: 1,
            monthsTitle: 'Месяцы'
        };
        const elem = document.getElementById('foo');
        const rangepicker = new DateRangePicker(elem, {
            language: 'ru'
        }); 
        
    }


    // Калькулятор Умный фермер
    // Мониторинг рождения телят
    if(document.getElementById('form4')){

        // пересчет цен за голову
        let inputZagolovu;
        const initInputZagolovu = ()=>{
            inputZagolovu = document.querySelectorAll('.form4__za-golovu input')

            inputZagolovu.forEach(el => el.addEventListener('input', ()=>{
                countPrice(el)
                formCounter();
            }))
        }

        initInputZagolovu()

        const countPrice = (el)=>{
            let val = el.value
            el.closest('.form4__za-golovu').querySelector('.premia').textContent = val * 0.25
        }

        // Добавление блока
        let form4__wrap = document.querySelector('.form4__wrap')
        let form4__wrap__row = document.querySelector('.form4__wrap__row')
        let addForm4Row = document.getElementById('add-form4-row')

        addForm4Row.addEventListener('click', ()=>{
            let clone = form4__wrap__row.cloneNode(true)
            form4__wrap.appendChild(clone)

            initInputZagolovu()
            formCounter()

            pristine = new Pristine(form,{
                classTo: 'form-group',
                errorClass: 'error',
                successClass: 'has-success',
                // class of the parent element where error text element is appended
                errorTextParent: 'form-group',
                // type of element to create for the error text
                errorTextTag: 'div',
                // class of the error text element
                errorTextClass: 'error__text' 
            });
        })


        // Пересчет общей цены
        let total4__sum = document.getElementById('total4__sum')
        let total4__sum2 = document.getElementById('total4__sum2')

        const formCounter = () =>{
            let totalSum = 0;
            inputZagolovu.forEach((el)=>{
                totalSum =  totalSum + Number(el.value)
                total4__sum.textContent =  totalSum.toFixed(2)
            })

            let premiaSum = 0;
            let premia = document.querySelectorAll('.premia')
            premia.forEach((el)=>{
                premiaSum =  premiaSum + Number(el.textContent)
                total4__sum2.textContent = premiaSum.toFixed(2)
            })
        }

        formCounter()
    }


    if(document.getElementById('form5')){
    
        // крс
        let krsCount = document.getElementById('krsCount')
        let krsZa1 = document.getElementById('krsZa1')
        let krsSum = document.getElementById('krsSum')
        let krsPrem = document.getElementById('krsPrem')

        const krsPremCount = ()=>{
            krsPrem.textContent = krsSum.value * 0.25
            formCounter()
        }

        krsSum.addEventListener('input', ()=>{
            if(krsCount.value < 1){
                krsCount.value = 1
            }
            krsZa1.value =  krsSum.value / krsCount.value
          
            krsPremCount()
        })

        krsCount.addEventListener('input', ()=>{
            krsSum.value = krsZa1.value * krsCount.value
            
            krsPremCount()
        })

        krsZa1.addEventListener('input', ()=>{
            krsSum.value = krsZa1.value * krsCount.value
           
            krsPremCount()
        })

        // мрс
        let mrsCount = document.getElementById('mrsCount')
        let mrsZa1 = document.getElementById('mrsZa1')
        let mrsSum = document.getElementById('mrsSum')
        let mrsPrem = document.getElementById('mrsPrem')

        const mrsPremCount = ()=>{
            mrsPrem.textContent = mrsSum.value * 0.25
            formCounter()
        }

        mrsSum.addEventListener('input', ()=>{
            if(mrsCount.value < 1){
                mrsCount.value = 1
            }
            mrsZa1.value =  mrsSum.value / mrsCount.value
          
            mrsPremCount()
        })

        mrsCount.addEventListener('input', ()=>{
            mrsSum.value = mrsZa1.value * mrsCount.value
        
            mrsPremCount()
        })

        mrsZa1.addEventListener('input', ()=>{
            mrsSum.value = mrsZa1.value * mrsCount.value
           
            mrsPremCount()
        })

        const formCounter = ()=>{
            
            form5total.textContent = Number(krsPrem.textContent) + Number(mrsPrem.textContent)
        }
        formCounter()

    }

})
